@import url(https://fonts.googleapis.com/css2?family=DotGothic16&family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,700&display=swap);
#root{
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
body {
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;

}



.App {

  text-align: center;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(#1e3c72 , #2a5298);
  /* background-image: linear-gradient(#ff758c , #ff7eb3); */
  
}


.App-header {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
}
.navigation{
  height: 10%;
  width: 100%;
background: #ff9800;
}
.form{
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.logo-container{
  height: 100%;
  width: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 1%;
}
.App-logo{
  height: 100%;
  width: 100%;
}

.input{
  width: 100%;
  height: 50%;
  background: transparent;
  border: none;
  text-decoration: none;
  cursor: pointer;
  margin: 1%;
  border: pink solid;
  background: white;
  border-radius: 10px;
}
.button{
  cursor: pointer;
  border: solid transparent 1px;
  height: 70%;
  width: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 16px;
  margin: 1%;
  transition: background-color 1s;
  background: red;
}
.button:hover{
  background-color: #80d0c7

}

.card-wrapper{
    height: 70%;
    width: 25%;
    background-image: linear-gradient(#13547a , #80d0c7);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    border-radius: 25px;
    box-shadow: -5px -5px 30px 5px red, 5px 5px 30px 5px blue;
    
}

.weather-image-container{
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.weather-icon{
color: white;
 height: 60%;
 width: 60%;
}
.temperature-container{
    height: 30%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;

    -webkit-flex-direction: column;

            flex-direction: column;
}
.current-temp{
    color: green;
    font-size: 65px;
}
.min-max{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 5px;
}
.min-temp{
color: blue;
margin: 5px;
}
.max-temp{
    color: red;
    margin: 5px;
}


.location-container{
        height: 30%;
        width: 100%;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
}
